html {
  --hover-color: #00acdc;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header_bar {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  height: 40px;
}

.navigation_bar {
  align-items: center;
  display: flex;
}

.navigation_bar__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation_bar__link {
  margin: 0 10px;
}

.link {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.navigation_bar__link:first-child {
  margin-left: 0;
}

.navigation_bar__link:last-child {
  margin-right: 0;
}

.link:hover, .is-active {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}
